<template>
    <div>

        <div v-if="page_loader" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpRequestStore.httpRequestMainAppMessage }}
            </div>
        </div>

        <div v-if="page_load_status.error" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpRequestStore.httpRequestMainAppMessage }}
            </div>
        </div>

        <div v-if="page_display">

            <div class="page-header">
                <h2>Manage User - {{ username_display }}</h2>
            </div>

            <div class="row">
                <side-menu></side-menu>
                <div class="col-md-9">
                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title">User Profile <router-link v-if="permission.update_user_security" :to="{ name: 'user-security', params: { id: $route.params.id }}"><i class="glyphicon glyphicon-lock"></i></router-link> <router-link v-if="authUserStore.permission.is_admin && usergroup_static < 5" :to="{ name: 'user-permission', params: { id: $route.params.id }}"><i class="glyphicon glyphicon-scale"></i></router-link> <router-link v-if="authUserStore.permission.is_admin" :to="{ name: 'user-duration', params: { id: $route.params.id }}"><i class="glyphicon glyphicon-time"></i></router-link> <router-link v-if="permission.update_user_credits" :to="{ name: 'user-credits', params: { id: $route.params.id }}"><i class="fa fa-bars"></i></router-link> <router-link v-if="permission.update_user_credits" :to="{ name: 'user-voucher', params: { id: $route.params.id }}"><i class="fa fa-qrcode"></i></router-link></h3>
                        </div><!-- end .panel-heading -->

                        <div class="panel-body">

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <form class="form-horizontal" v-on:submit.prevent="edituser_trigger">

                                <template v-if="permission.update_user_usergroup">
                                    <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.user_group_id}">
                                        <label for="usergroup" class="col-sm-3 control-label">User Group</label>
                                        <div class="col-sm-9">
                                            <select class="form-control" :disabled="process_loader" id="usergroup" name="usergroup" v-model="user.user_group_id">
                                                <template v-for='option in user_groups'>
                                                    <option :value="option.id" :selected="option.id == user.user_group_id">{{ option.name }}</option>
                                                </template>
                                            </select>
                                            <small v-if="!process_loader" v-for="error in inputErrors.user_group_id" class="help-block">
                                                <strong>{{ error }}</strong>
                                            </small>
                                        </div>
                                    </div>
                                </template>
                                <template v-else>
                                    <div class="form-group">
                                        <label class="col-sm-3 control-label">User Group</label>
                                        <div class="col-sm-9">
                                            <p class="form-control-static"><span :class="'label label-' + $auth.userGroup(user.user_group_id).class_type">{{ $auth.userGroup(user.user_group_id).name_type }}</span></p>
                                        </div>
                                    </div>
                                </template>


                                <!-- start for Username -->
                                <template v-if="authUserStore.permission.is_admin">
                                    <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.username}">
                                        <label for="username" class="col-sm-3 control-label">Username</label>
                                        <div class="col-sm-9">
                                            <input type="text" class="form-control" :disabled="process_loader" id="username" name="username" v-model.trim="user.username" placeholder="Username">
                                            <small v-if="!process_loader" v-for="error in inputErrors.username" class="help-block">
                                                <strong>{{ error }}</strong>
                                            </small>
                                        </div>
                                    </div>
                                </template>
                                <template v-else>
                                    <div class="form-group">
                                        <label for="username" class="col-sm-3 control-label">Username</label>
                                        <div class="col-sm-9">
                                            <p class="form-control-static">{{ user.username }}</p>
                                        </div>
                                    </div>
                                </template>
                                <!-- end for Username -->

                                <!-- start for Email -->
                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.email}">
                                    <label for="email" class="col-sm-3 control-label">Email</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="email" name="email" v-model.trim="user.email" placeholder="Email">
                                        <small v-if="!process_loader" v-for="error in inputErrors.email" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>
                                <!-- end for Email -->

                                <!-- start for Fullname -->
                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.fullname}">
                                    <label for="fullname" class="col-sm-3 control-label">Fullname</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="fullname" name="fullname" v-model.trim="user.fullname" placeholder="Fullname">
                                        <small v-if="!process_loader" v-for="error in inputErrors.fullname" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>
                                <!-- end for Fullname -->

                                <template v-if="permission.update_user_status">
                                    <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.status}">
                                        <label for="status" class="col-sm-3 control-label">Status</label>
                                        <div class="col-sm-9">
                                            <select class="form-control" id="status" :disabled="process_loader" name="status" v-model="user.status_id">
                                                <template v-for='option in user_status'>
                                                    <option :value="option.id" :selected="option.id == user.status_id">{{ option.name }}</option>
                                                </template>
                                            </select>
                                            <small v-if="!process_loader" v-for="error in inputErrors.status" class="help-block">
                                                <strong>{{ error }}</strong>
                                            </small>
                                        </div>
                                    </div>
                                </template>
                                <template v-else>
                                    <div class="form-group">
                                        <label class="col-sm-3 control-label">Status :</label>
                                        <div class="col-sm-9">
                                            <p class="form-control-static"><span :class="'label label-' + $auth.userStatus(user.status_id).class_type">{{ $auth.userStatus(user.status_id).name_type }}</span></p>
                                        </div>
                                    </div>
                                </template>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Expiration Date :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static"><span :class="{'label ': true, 'label-default': user.expired_at === 'Expired', 'label-success': user.expired_at !== 'Expired'}">{{ user.expired_at }}</span></p>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Date Registered :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static">{{ user.created_at }}</p>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Last Update :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static">{{ user.updated_at }}</p>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <div class="col-sm-offset-3 col-sm-9">
                                        <button type="submit" class="btn btn-primary" :disabled="process_loader">Save</button>
                                    </div>
                                </div>

                            </form><!-- end .form-horizontal -->

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->
                </div><!-- end .col-md-6 -->
            </div><!-- end .row -->
        </div><!-- end v-if="!fetchUserDataStatus.loading && !fetchUserDataStatus.error" -->
        <top-progress ref="topProgress"></top-progress>
    </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {accountUrl, userUrl, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default{
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    console.log('Edit User created.')
    if (!this.$store.state.httpRequestStore.httpRequestMainApp) {
      this.$store.dispatch('setHttpRequestMainApp', true)
      this.$store.dispatch('setHttpRequestMainAppHttpCode', 0)
      this.$store.dispatch('setHttpRequestMainAppMessage', 'Accessing Page...')
      this.$store.dispatch('setHttpRequestMainAppAccess', false)
      this.fetchAccountData()
    }
    this.fetchUserData()
  },

  components: {
    SideMenu,
    topProgress
  },

  data () {
    return {
      username_display: '',
      usergroup_static: 0,
      user: {},
      httpLoad: {
        process: true,
        httpCode: 0,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      user_status: [
        {id: 0, name: 'Deactivated'},
        {id: 1, name: 'Active'},
        {id: 2, name: 'Suspended'}
      ],
      inputErrors: [],
      permission: {}
    }
  },

  computed: {
    page_loader () {
      return this.$store.state.httpRequestStore.httpRequestMainApp || this.httpLoad.process
    },
    page_load_status () {
      if (!this.page_loader) {
        if (this.$store.state.httpRequestStore.httpRequestMainAppHttpCode === 200) {
          if (this.httpLoad.httpCode !== 200) {
            this.$refs.topProgress.fail()
            this.$store.dispatch('setHttpRequestMainAppMessage', this.httpLoad.message)
          } else if (this.httpLoad.httpCode === 200) {
            this.$refs.topProgress.done()
            this.$store.dispatch('setHttpRequestMainAppAccess', true)
          }
        } else {
          this.$refs.topProgress.fail()
        }
      }

      return {
        error: !this.page_loader && !this.$store.state.httpRequestStore.httpRequestMainAppAccess,
        message: this.$store.state.httpRequestStore.httpRequestMainAppMessage
      }
    },
    page_display () {
      return !this.page_loader && this.$store.state.httpRequestStore.httpRequestMainAppAccess
    },

    user_groups () {
      if (this.$store.state.authUserStore.usergroup === 1) {
        return [{id: 2, name: 'Ultimate'}, {id: 3, name: 'Premium'}, {id: 4, name: 'Reseller'}, {id: 5, name: 'Client'}]
      } else if (this.$store.state.authUserStore.usergroup === 2) {
        return [{id: 3, name: 'Premium'}, {id: 4, name: 'Reseller'}, {id: 5, name: 'Client'}]
      } else if (this.$store.state.authUserStore.usergroup === 3) {
        return [{id: 4, name: 'Reseller'}, {id: 5, name: 'Client'}]
      } else {
        return [{id: 5, name: 'Client'}]
      }
    },

    process_loader () {
      return this.httpRequest.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  methods: {
    fetchAccountData () {
      this.$http.get(accountUrl, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        this.$store.dispatch('setHttpRequestMainAppHttpCode', response.status)

        this.$store.dispatch('setUserName', response.data.username)
        this.$store.dispatch('setUserEmail', response.data.email)
        this.$store.dispatch('setUserFullname', response.data.fullname)
        this.$store.dispatch('setUserStatus', response.data.status_id)
        this.$store.dispatch('setUserGroup', response.data.user_group_id)
        this.$store.dispatch('setUserReg', response.data.created_at)
        this.$store.dispatch('setUserUpdate', response.data.updated_at)

        var permission = {}
        permission = response.data.permission
        this.$store.dispatch('setUserPermission', permission)

        this.$auth.setOneUserToken('username', response.data.username)

        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 500)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainAppHttpCode', errorHttpCode)
          this.$store.dispatch('setHttpRequestMainAppMessage', errorMessage)
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 500)
        debounce()
      })
    },

    fetchUserData () {
      this.$http.get(userUrl(this.$route.params.id), {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        this.httpLoad.httpCode = response.status

        this.user.user_group_id = response.data.user.user_group_id
        this.user.username = response.data.user.username
        this.user.email = response.data.user.email
        this.user.fullname = response.data.user.fullname
        this.user.status_id = response.data.user.status_id
        this.user.expired_at = response.data.user.expired_at
        this.user.created_at = response.data.user.created_at
        this.user.updated_at = response.data.user.updated_at

        this.username_display = this.user.username
        this.usergroup_static = this.user.user_group_id

        this.permission = response.data.permission

        var debounce = _.debounce(() => {
          this.$Progress.finish()
          this.httpLoad.process = false
        }, 2000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.$Progress.fail()
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 2000)
        debounce()
      })
    },

    hideMsg () {
      this.httpRequest.trigger = false
      this.inputErrors = []
    },

    edituser_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Updating User...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.edit_user(), 500)
        debounce()
      }
    },

    edit_user () {
      this.$http.post(userUrl(this.$route.params.id), this.user, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)
        this.httpRequest.httpCode = response.status

        var debounce = _.debounce(() => {
          this.$Progress.finish()
          this.user.username = response.data.user.username
          this.username_display = this.user.username
          this.user.user_group_id = response.data.user.user_group_id
          this.usergroup_static = this.user.user_group_id
          this.user.status_id = response.data.user.status_id
          this.user.updated_at = response.data.user.updated_at
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.$Progress.fail()
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    }
  }
}
</script>



