<template>
    <div>

        <div v-if="page_loader" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpRequestStore.httpRequestMainAppMessage }}
            </div>
        </div>

        <div v-if="page_load_status.error" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpRequestStore.httpRequestMainAppMessage }}
            </div>
        </div>

        <div v-if="page_display">

            <div class="page-header">
                <h2>Account Profile</h2>
            </div>

            <div class="row">
                <side-menu></side-menu>
                <div class="col-md-9">

                    <div class="row">
                        <div class="col-md-3 col-sm-6 col-xs-12">
                            <div class="info-box">
                                <span class="info-box-icon bg-green"><i class="glyphicon glyphicon-user"></i></span>

                                <div class="info-box-content">
                                    <span class="info-box-text">User Group</span>
                                    <span class="info-box-number"><span :class="'label label-' + $auth.userGroup(account.user_group_id).class_type">{{ $auth.userGroup(account.user_group_id).name_type }}</span></span>
                                </div>
                                <!-- /.info-box-content -->
                            </div>
                            <!-- /.info-box -->
                        </div>
                        <div class="col-md-3 col-sm-6 col-xs-12">
                            <div class="info-box">
                                <span class="info-box-icon bg-aqua"><i class="glyphicon glyphicon-certificate"></i></span>

                                <div class="info-box-content">
                                    <span class="info-box-text">Credits</span>
                                    <span class="info-box-number">
                                        <template v-if="account.credits==='No Limit'">
                                            <span class="label label-success">{{ account.credits }}</span>
                                        </template>
                                        <template v-else>
                                            <span :class="{'label ': true, 'label-primary': account.credits > 0, 'label-danger': account.credits == 0}">{{ account.credits }}</span>
                                        </template>
                                    </span>
                                </div>
                                <!-- /.info-box-content -->
                            </div>
                            <!-- /.info-box -->
                        </div>
                        <div class="col-md-3 col-sm-6 col-xs-12">
                            <div class="info-box">
                                <span class="info-box-icon bg-red"><i class="glyphicon glyphicon-time"></i></span>

                                <div class="info-box-content">
                                    <span class="info-box-text">Expiration</span>
                                    <span class="info-box-number"><span :class="{'label ': true, 'label-default': authUserStore.expiration === 'Expired', 'label-success': authUserStore.expiration !== 'Expired'}">{{ authUserStore.expiration }}</span></span>
                                </div>
                                <!-- /.info-box-content -->
                            </div>
                            <!-- /.info-box -->
                        </div>
                        <div class="col-md-3 col-sm-6 col-xs-12">
                            <div class="info-box">
                                <span class="info-box-icon bg-yellow"><i class="glyphicon glyphicon-info-sign"></i></span>

                                <div class="info-box-content">
                                    <span class="info-box-text">Status</span>
                                    <span class="info-box-number"><span :class="'label label-' + $auth.userStatus(account.status_id).class_type">{{ $auth.userStatus(account.status_id).name_type }}</span></span>
                                </div>
                                <!-- /.info-box-content -->
                            </div>
                            <!-- /.info-box -->
                        </div>
                    </div>

                    <div class="panel panel-default">
                        <div class="panel-heading">
                            <h3 class="panel-title">Profile</h3>
                        </div>
                        <div class="panel-body">

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <form class="form-horizontal" v-on:submit.prevent="editaccount_trigger">

                                <!--
                                <div class="form-group">
                                    <label class="col-sm-3 control-label">User Group</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static"><span :class="'label label-' + $auth.userGroup(account.user_group_id).class_type">{{ $auth.userGroup(account.user_group_id).name_type }}</span></p>
                                    </div>
                                </div>
                                -->

                                <!-- start for Username -->
                                <template v-if="authUserStore.permission.is_admin">
                                    <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.username}">
                                        <label for="username" class="col-sm-3 control-label">Username</label>
                                        <div class="col-sm-9">
                                            <input type="text" class="form-control" :disabled="process_loader || !authUserStore.permission.update_account" id="username" name="username" v-model.trim="account.username" placeholder="Username">
                                            <small v-if="!process_loader" v-for="error in inputErrors.username" class="help-block">
                                                <strong>{{ error }}</strong>
                                            </small>
                                        </div>
                                    </div>
                                </template>
                                <template v-else>
                                    <div class="form-group">
                                        <label for="username" class="col-sm-3 control-label">Username</label>
                                        <div class="col-sm-9">
                                            <p class="form-control-static">{{ account.username }}</p>
                                        </div>
                                    </div>
                                </template>
                                <!-- end for Username -->

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.email}">
                                    <label for="email" class="col-sm-3 control-label">Email</label>
                                    <div class="col-sm-9">
                                        <input type="email" class="form-control" :disabled="process_loader || !authUserStore.permission.update_account" id="email" name="email" v-model.trim="account.email" placeholder="Email">
                                        <small v-show="!process_loader" v-for="error in inputErrors.email" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.fullname}">
                                    <label for="fullname" class="col-sm-3 control-label">Fullname</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader || !authUserStore.permission.update_account" id="fullname" name="fullname" v-model.trim="account.fullname" placeholder="Fullname">
                                        <small v-show="!process_loader" v-for="error in inputErrors.fullname" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <!--
                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Status :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static"><span :class="'label label-' + $auth.userStatus(account.status_id).class_type">{{ $auth.userStatus(account.status_id).name_type }}</span></p>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Credits :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static">
                                            <template v-if="account.credits==='No Limit'">
                                                <span class="label label-success">{{ account.credits }}</span>
                                            </template>
                                            <template v-else>
                                                <span :class="{'label ': true, 'label-primary': account.credits > 0, 'label-danger': account.credits == 0}">{{ account.credits }}</span>
                                            </template>
                                        </p>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Expiration Date :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static"><span :class="{'label ': true, 'label-default': authUserStore.expiration === 'Expired', 'label-success': authUserStore.expiration !== 'Expired'}">{{ authUserStore.expiration }}</span></p>
                                    </div>
                                </div>
                                -->

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Date Registered :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static">{{ account.created_at }}</p>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-3 control-label">Last Update :</label>
                                    <div class="col-sm-9">
                                        <p class="form-control-static">{{ account.updated_at }}</p>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <div class="col-sm-offset-3 col-sm-9">
                                        <button type="submit" class="btn btn-primary" :disabled="process_loader || !authUserStore.permission.update_account">Save</button>
                                    </div>
                                </div>

                            </form>

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->
                </div><!-- end .col-md-9 -->
            </div><!-- end .row -->

        </div><!-- v-if="!fetchUserDataStatus.loading && !fetchUserDataStatus.error && permission.update_account" -->
        <top-progress ref="topProgress"></top-progress>
    </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {accountUrl, accountUrlUpdate, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default {
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    console.log('Account Profile created.')
    if (!this.$store.state.httpRequestStore.httpRequestMainApp) {
      this.$store.dispatch('setHttpRequestMainApp', true)
      this.$store.dispatch('setHttpRequestMainAppHttpCode', 0)
      this.$store.dispatch('setHttpRequestMainAppMessage', 'Accessing Page...')
      this.$store.dispatch('setHttpRequestMainAppAccess', false)
      this.fetchAccountData()
    }
  },

  components: {
    SideMenu,
    topProgress
  },

  data () {
    return {
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      inputErrors: []
    }
  },

  computed: {
    page_loader () {
      return this.$store.state.httpRequestStore.httpRequestMainApp
    },
    page_load_status () {
      if (!this.page_loader) {
        if (this.$store.state.httpRequestStore.httpRequestMainAppHttpCode === 200) {
          this.$refs.topProgress.done()
          this.$store.dispatch('setHttpRequestMainAppAccess', true)
        } else {
          this.$refs.topProgress.fail()
        }
      }

      return {
        error: !this.page_loader && !this.$store.state.httpRequestStore.httpRequestMainAppAccess,
        message: this.$store.state.httpRequestStore.httpRequestMainAppMessage
      }
    },
    page_display () {
      this.load_account
      return !this.page_loader && this.$store.state.httpRequestStore.httpRequestMainAppAccess
    },

    process_loader () {
      return this.httpRequest.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    account () {
      const account = {}
      account.user_group_id = this.$store.state.authUserStore.usergroup
      account.username = this.$store.state.authUserStore.username
      account.email = this.$store.state.authUserStore.email
      account.fullname = this.$store.state.authUserStore.fullname
      account.credits = this.$store.state.authUserStore.credits
      account.status_id = this.$store.state.authUserStore.status
      account.created_at = this.$store.state.authUserStore.datereg
      account.updated_at = this.$store.state.authUserStore.lastupdate
      return account
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  methods: {
    hideMsg () {
      this.httpRequest.trigger = false
      this.inputErrors = []
    },

    editaccount_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Updating your account...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.edit_account(), 500)
        debounce()
      }
    },

    edit_account () {
      this.$http.post(accountUrlUpdate, this.account, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)
        this.httpRequest.httpCode = response.status

        var debounce = _.debounce(() => {
          if (this.account.username) {
            this.$auth.setOneUserToken('username', this.account.username)
            this.$store.dispatch('setUserName', this.$auth.getUserToken().username)
          }
          this.$store.dispatch('setUserUpdate', response.data.account.updated_at)
          this.httpRequest.message = response.data.message
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    fetchAccountData () {
      console.log('Local fetchUserData')
      this.$http.get(accountUrl, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        this.$store.dispatch('setHttpRequestMainAppHttpCode', response.status)

        this.$store.dispatch('setUserName', response.data.username)
        this.$store.dispatch('setUserEmail', response.data.email)
        this.$store.dispatch('setUserFullname', response.data.fullname)
        this.$store.dispatch('setUserStatus', response.data.status_id)
        this.$store.dispatch('setUserGroup', response.data.user_group_id)
        this.$store.dispatch('setUserCredits', response.data.credits)
        this.$store.dispatch('setUserExpiration', response.data.expired_at)
        this.$store.dispatch('setUserReg', response.data.created_at)
        this.$store.dispatch('setUserUpdate', response.data.updated_at)

        var permission = {}
        permission = response.data.permission
        this.$store.dispatch('setUserPermission', permission)

        this.$auth.setOneUserToken('username', response.data.username)

        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 500)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainAppHttpCode', errorHttpCode)
          this.$store.dispatch('setHttpRequestMainAppMessage', errorMessage)
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 500)
        debounce()
      })
    }
  }
}
</script>



