<template>
    <div>

        <div v-if="page_loader" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpRequestStore.httpRequestMainAppMessage }}
            </div>
        </div>

        <div v-if="page_load_status.error" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpRequestStore.httpRequestMainAppMessage }}
            </div>
        </div>

        <div v-if="page_display">

            <div class="page-header">
                <h2>Manage User - {{ username_display }}</h2>
            </div>

            <div class="row">
                <side-menu></side-menu>
                <div class="col-md-9">
                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title"><router-link :to="{ name: 'user-profile', params: { id: $route.params.id }}"><i class="glyphicon glyphicon-arrow-left"></i></router-link> User Permission</h3>
                        </div><!-- end .panel-heading -->

                        <div class="panel-body">

                            <div v-if="process_loader" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    Updating user permission...
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ process_status.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    User permission updated.
                                </div>
                            </div>

                            <form class="form-horizontal" v-on:submit.prevent="">

                                <div class="form-group">
                                    <label class="col-sm-5 control-label">Create Account :</label>
                                    <div class="col-sm-6">
                                        <button :class="{'btn btn-sm': true, 'btn-danger': !pcode[0].value, 'btn-success': pcode[0].value}" :disabled="process_loader" @click="edituserpermission_trigger(0)">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</button>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-5 control-label">Update User Account :</label>
                                    <div class="col-sm-6">
                                        <button :class="{'btn btn-sm': true, 'btn-danger': !pcode[1].value, 'btn-success': pcode[1].value}" :disabled="process_loader" @click="edituserpermission_trigger(1)">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</button>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-5 control-label">Update User Password</label>
                                    <div class="col-sm-6">
                                        <button :class="{'btn btn-sm': true, 'btn-danger': !pcode[2].value, 'btn-success': pcode[2].value}" :disabled="process_loader" @click="edituserpermission_trigger(2)">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</button>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-5 control-label">Update User Group</label>
                                    <div class="col-sm-6">
                                        <button :class="{'btn btn-sm': true, 'btn-danger': !pcode[10].value, 'btn-success': pcode[10].value}" :disabled="process_loader" @click="edituserpermission_trigger(10)">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</button>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-5 control-label">Set New User Status</label>
                                    <div class="col-sm-6">
                                        <button :class="{'btn btn-sm': true, 'btn-danger': !pcode[3].value, 'btn-success': pcode[3].value}" :disabled="process_loader" @click="edituserpermission_trigger(3)">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</button>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-5 control-label">Update User Status</label>
                                    <div class="col-sm-6">
                                        <button :class="{'btn btn-sm': true, 'btn-danger': !pcode[4].value, 'btn-success': pcode[4].value}" :disabled="process_loader" @click="edituserpermission_trigger(4)">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</button>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-5 control-label">Delete User</label>
                                    <div class="col-sm-6">
                                        <button :class="{'btn btn-sm': true, 'btn-danger': !pcode[5].value, 'btn-success': pcode[5].value}" :disabled="process_loader" @click="edituserpermission_trigger(5)">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</button>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-5 control-label">Manage Other User Account</label>
                                    <div class="col-sm-6">
                                        <button :class="{'btn btn-sm': true, 'btn-danger': !pcode[6].value, 'btn-success': pcode[6].value}" :disabled="process_loader || user_group > 2" @click="edituserpermission_trigger(6)">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</button> <span>(Available for Ultimate User)</span>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-5 control-label">Manage Other User Password</label>
                                    <div class="col-sm-6">
                                        <button :class="{'btn btn-sm': true, 'btn-danger': !pcode[7].value, 'btn-success': pcode[7].value}" :disabled="process_loader || user_group > 2" @click="edituserpermission_trigger(7)">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</button> (Available for Ultimate User)
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-5 control-label">Manage Other User Group</label>
                                    <div class="col-sm-6">
                                        <button :class="{'btn btn-sm': true, 'btn-danger': !pcode[11].value, 'btn-success': pcode[11].value}" :disabled="process_loader || user_group > 2" @click="edituserpermission_trigger(11)">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</button> (Available for Ultimate User)
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-5 control-label">Manage Other User Status</label>
                                    <div class="col-sm-6">
                                        <button :class="{'btn btn-sm': true, 'btn-danger': !pcode[8].value, 'btn-success': pcode[8].value}" :disabled="process_loader || user_group > 2" @click="edituserpermission_trigger(8)">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</button> (Available for Ultimate User)
                                    </div>
                                </div>

                                <div class="form-group">
                                    <label class="col-sm-5 control-label">Delete Other User</label>
                                    <div class="col-sm-6">
                                        <button :class="{'btn btn-sm': true, 'btn-danger': !pcode[9].value, 'btn-success': pcode[9].value}" :disabled="process_loader || user_group > 2" @click="edituserpermission_trigger(9)">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</button> (Available for Ultimate User)
                                    </div>
                                </div>

                            </form><!-- end .form-horizontal -->

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->
                </div><!-- end .col-md-6 -->
            </div><!-- end .row -->
        </div><!-- end v-if="!fetchUserDataStatus.loading && !fetchUserDataStatus.error" -->
        <top-progress ref="topProgress"></top-progress>
    </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {accountUrl, userUrlPermision, userUrlPermisionToggle, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default{
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    console.log('Edit User created.')
    if (!this.$store.state.httpRequestStore.httpRequestMainApp) {
      this.$store.dispatch('setHttpRequestMainApp', true)
      this.$store.dispatch('setHttpRequestMainAppHttpCode', 0)
      this.$store.dispatch('setHttpRequestMainAppMessage', 'Accessing Page...')
      this.$store.dispatch('setHttpRequestMainAppAccess', false)
      this.fetchAccountData()
    }
    this.fetchUserData()
  },

  components: {
    SideMenu,
    topProgress
  },

  data () {
    return {
      username_display: '',
      user_group: 0,
      httpLoad: {
        process: true,
        httpCode: 0,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      permission: {},
      p_code: [],
      pcode: [
        {code: 'PCODE_001', value: false},
        {code: 'PCODE_002', value: false},
        {code: 'PCODE_003', value: false},
        {code: 'PCODE_004', value: false},
        {code: 'PCODE_005', value: false},
        {code: 'PCODE_006', value: false},
        {code: 'PCODE_007', value: false},
        {code: 'PCODE_008', value: false},
        {code: 'PCODE_009', value: false},
        {code: 'PCODE_010', value: false},
        {code: 'PCODE_011', value: false},
        {code: 'PCODE_012', value: false},
        {code: 'PCODE_013', value: false},
        {code: 'PCODE_014', value: false}
      ]
    }
  },

  computed: {
    page_loader () {
      return this.$store.state.httpRequestStore.httpRequestMainApp || this.httpLoad.process
    },
    page_load_status () {
      if (!this.page_loader) {
        if (this.$store.state.httpRequestStore.httpRequestMainAppHttpCode === 200) {
          if (this.httpLoad.httpCode !== 200) {
            this.$refs.topProgress.fail()
            this.$store.dispatch('setHttpRequestMainAppMessage', this.httpLoad.message)
          } else if (this.httpLoad.httpCode === 200) {
            this.$refs.topProgress.done()
            this.$store.dispatch('setHttpRequestMainAppAccess', true)
          }
        } else {
          this.$refs.topProgress.fail()
        }
      }

      return {
        error: !this.page_loader && !this.$store.state.httpRequestStore.httpRequestMainAppAccess,
        message: this.$store.state.httpRequestStore.httpRequestMainAppMessage
      }
    },

    page_display () {
      return !this.page_loader && this.$store.state.httpRequestStore.httpRequestMainAppAccess
    },

    user_groups () {
      if (this.$store.state.authUserStore.usergroup === 1) {
        return [{id: 2, name: 'Ultimate'}, {id: 3, name: 'Premium'}, {id: 4, name: 'Reseller'}, {id: 5, name: 'Client'}]
      } else if (this.$store.state.authUserStore.usergroup === 2) {
        return [{id: 3, name: 'Premium'}, {id: 4, name: 'Reseller'}, {id: 5, name: 'Client'}]
      } else if (this.$store.state.authUserStore.usergroup === 3) {
        return [{id: 4, name: 'Reseller'}, {id: 5, name: 'Client'}]
      } else {
        return [{id: 5, name: 'Client'}]
      }
    },

    process_loader () {
      return this.httpRequest.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  methods: {
    fetchAccountData () {
      this.$http.get(accountUrl, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log('Edit User: ' + response)

        this.$store.dispatch('setHttpRequestMainAppHttpCode', response.status)

        this.$store.dispatch('setUserName', response.data.username)
        this.$store.dispatch('setUserEmail', response.data.email)
        this.$store.dispatch('setUserFullname', response.data.fullname)
        this.$store.dispatch('setUserStatus', response.data.status_id)
        this.$store.dispatch('setUserGroup', response.data.user_group_id)
        this.$store.dispatch('setUserReg', response.data.created_at)
        this.$store.dispatch('setUserUpdate', response.data.updated_at)

        var permission = {}
        permission = response.data.permission
        this.$store.dispatch('setUserPermission', permission)

        this.$auth.setOneUserToken('username', response.data.username)

        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 500)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainAppHttpCode', errorHttpCode)
          this.$store.dispatch('setHttpRequestMainAppMessage', errorMessage)
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 500)
        debounce()
      })
    },

    fetchUserData () {
      this.$http.get(userUrlPermision(this.$route.params.id), {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        this.httpLoad.httpCode = response.status

        this.user_group = response.data.user.user_group_id
        this.username_display = response.data.user.username

        this.p_code = response.data.user.roles

        for (let s of this.p_code) {
          this.togglePermission(s.id - 1)
        }

        var debounce = _.debounce(() => {
          this.httpLoad.process = false
        }, 2000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 2000)
        debounce()
      })
    },

    togglePermission (id) {
      this.pcode[id].value = !this.pcode[id].value
    },

    hideMsg () {
      this.httpRequest.trigger = false
      this.inputErrors = []
    },

    edituserpermission_trigger (pCode) {
      if (!this.process_loader) {
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.edituser_permission(pCode), 500)
        debounce()
      }
    },

    edituser_permission (pCode) {
      this.$http.get(userUrlPermisionToggle(this.$route.params.id, pCode + 1), {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)
        this.httpRequest.httpCode = response.status

        var debounce = _.debounce(() => {
          this.togglePermission(pCode)
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        if (error.response) {
          this.httpRequest.httpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            this.httpRequest.message = error.response.data.message
          } else {
            this.httpRequest.message = 'Server Error.'
          }
        } else {
          this.httpRequest.httpCode = 0
          this.httpRequest.message = error.message
        }
        var debounce = _.debounce(() => {
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    }
  }
}
</script>



