<template>
  <div>
    <top-menu></top-menu>
    <router-view></router-view>
    <!-- set progressbar -->
    <vue-progress-bar></vue-progress-bar>
  </div>
</template>

<style>
    /*
 * Component: Info Box
 * -------------------
 */
.info-box {
  display: block;
  min-height: 90px;
  background: #F5F5F5;
  width: 100%;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  margin-bottom: 15px;
}
.info-box small {
  font-size: 12px;
}
.info-box .progress {
  background: rgba(0, 0, 0, 0.2);
  margin: 5px -10px 5px -10px;
  height: 2px;
}
.info-box .progress,
.info-box .progress .progress-bar {
  border-radius: 0;
}
.info-box .progress .progress-bar {
  background: #fff;
}
.info-box-icon {
  border-top-left-radius: 2px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 2px;
  display: block;
  float: left;
  height: 90px;
  width: 90px;
  text-align: center;
  font-size: 45px;
  line-height: 90px;
  background: rgba(0, 0, 0, 0.2);
}
.info-box-icon > img {
  max-width: 100%;
}
.info-box-content {
  padding: 5px 10px;
  margin-left: 90px;
}
.info-box-number {
  display: block;
  font-weight: bold;
  font-size: 18px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.progress-description,
.info-box-text {
  display: block;
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.info-box-text {
  text-transform: uppercase;
}
.info-box-more {
  display: block;
}
.progress-description {
  margin: 0;
}
.bg-green,
.bg-aqua,
.bg-red,
.bg-yellow {
  color: #fff !important;
}
.bg-green {
  background-color: #00a65a !important;
}
.bg-aqua {
  background-color: #00c0ef !important;
}
.bg-red {
  background-color: #dd4b39 !important;
}

.bg-yellow {
  background-color: #f39c12 !important;
}
</style>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import TopMenu from './components/Topmenu.vue'
import {accountUrl, getHeader} from './config.js'
export default {
  created () {
    console.log('App created.')

    // check if user has userToken Active
    if (this.$auth.isAuth()) {
      // Initialize username for all pages
      this.$store.dispatch('setUserName', this.$auth.getUserToken().username)

      // Initialize userToken from localStorage
      this.$store.dispatch('setUserToken', this.$auth.getUserToken())

      const permission = {}
      permission.update_account = true
      this.$store.dispatch('setUserPermission', permission)

      // trigger loader
      this.$store.dispatch('setHttpRequestMainApp', true)
      this.$store.dispatch('setHttpRequestMainAppHttpCode', 0)
      this.$store.dispatch('setHttpRequestMainAppMessage', 'Accessing Page...')
      this.$store.dispatch('setHttpRequestMainAppAccess', false)

      this.fetchAccountData()
    } else { // else redirect to login page
      this.$auth.destroyUser()
      this.$store.dispatch('clearAuthUser')
      this.$router.push({name: 'login'})
    }
  },

  components: {
    TopMenu
  },

  computed: {
    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  methods: {
    fetchAccountData () {
      this.$http.get(accountUrl, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response.data)

        this.$store.dispatch('setHttpRequestMainAppHttpCode', response.status)

        this.$store.dispatch('setUserName', response.data.username)
        this.$store.dispatch('setUserEmail', response.data.email)
        this.$store.dispatch('setUserFullname', response.data.fullname)
        this.$store.dispatch('setUserStatus', response.data.status_id)
        this.$store.dispatch('setUserGroup', response.data.user_group_id)
        this.$store.dispatch('setUserCredits', response.data.credits)
        this.$store.dispatch('setUserExpiration', response.data.expired_at)
        this.$store.dispatch('setUserReg', response.data.created_at)
        this.$store.dispatch('setUserUpdate', response.data.updated_at)

        var permission = {}
        permission = response.data.permission
        this.$store.dispatch('setUserPermission', permission)

        this.$auth.setOneUserToken('username', response.data.username)

        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 500)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainAppHttpCode', errorHttpCode)
          this.$store.dispatch('setHttpRequestMainAppMessage', errorMessage)
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 1000)
        debounce()
      })
    } // end fetchUserData
  } // end methods
}
</script>



