<template>
<div>
    <div class="row top-login">
        <div class="panel panel-default">
            <div class="panel-heading">
                <h3 class="panel-title">Login Page</h3>
            </div>
            <div class="panel-body">

                <div v-if="httpRequest.process" class="loading">
                    <div class="alert alert-dismissible alert-info" role="alert">
                        <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                        Logging you in...
                    </div>
                </div>

                <div v-if="!httpRequest.process && httpRequest.trigger && httpRequest.httpCode !== 200" class="error">
                    <div class="alert alert-dismissible alert-danger" role="alert">
                        <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                        {{ httpRequest.message }}
                    </div>
                </div>

                <div v-if="!httpRequest.process && httpRequest.trigger && httpRequest.httpCode === 200" class="success">
                    <div class="alert alert-success" role="alert">
                        Login successfully.
                    </div>
                </div>

                <form class="form-horizontal" v-on:submit.prevent="login_trigger">
                    <div class="form-group">
                        <div class="col-md-12">
                            <input type="text" class="form-control" :disabled="httpRequest.process" v-model="username" id="username" name="username" value="" placeholder="Username">
                            <span class="glyphicon glyphicon-user form-control-feedback">&nbsp;&nbsp;</span>
                        </div>
                    </div>

                    <div class="form-group">
                        <div class="col-md-12">
                            <input type="password" class="form-control" :disabled="httpRequest.process" v-model="password" id="password" name="password" placeholder="Password">
                            <span class="glyphicon glyphicon-lock form-control-feedback">&nbsp;&nbsp;</span>
                        </div>
                    </div>

                    <div class="form-group pull-right">
                        <div class="col-md-12">
                            <button type="submit" class="btn btn-primary" :disabled="httpRequest.process">Login</button>
                        </div>
                    </div>
                </form>
            </div><!-- end .panel-body -->
        </div><!-- end .panel .panel-default -->
    </div><!-- end .row .top-login -->
</div>
</template>

<style>
    .top-login {
        margin: 0 auto;
        padding: 75px 0 0 0;
        width: 400px;
    }
    /* Full-width inputs */
    input[type=text], input[type=password] {
        display: inline-block;
        border: 1px solid #ccc;
        box-sizing: border-box;
    }
</style>

<script>
import _ from 'lodash'
import moment from 'moment'
import {loginUrl, accountUrl, getHeader} from './../config.js'
export default{
  created () {
    this.$store.dispatch('clearAuthUser')
    console.log('Login created.')
  },

  data () {
    return {
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      username: '',
      password: ''
    }
  },

  methods: {
    hideMsg () {
      this.httpRequest.trigger = false
    },
    login_trigger () {
      if (this.httpRequest.process === false) {
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        var debounceLogin = _.debounce(() => this.login(), 500)
        debounceLogin()
      }
    },

    login () {
      var loginData = {
        client_id: 2,
        client_secret: 'CzV7xWpvesw1CyXUBNgTAMtMigefaQwuo1QUXkkR',
        grant_type: 'password',
        username: this.username,
        password: this.password
      }

      this.$http.post(loginUrl, loginData).then(response => {
        console.log(response)
        // console.log('Status: ' + response.status)
        const userToken = {}
        userToken.access_token = response.data.access_token
        userToken.refresh_token = response.data.refresh_token
        userToken.expiration = moment.unix(parseInt(new Date(Date()).getTime() / 1000) + parseInt(response.data.expires_in)).format('YYYY-MM-DD HH:mm:ss')
        this.$http.get(accountUrl, {headers: getHeader(userToken)}).then(response => {
          console.log(response)
          if (response.data.status_id === 1) {
            this.$store.dispatch('setUserName', response.data.username)
            userToken.id = response.data.id
            userToken.username = response.data.username
            this.$auth.setUserToken(userToken)
            this.httpRequest.httpCode = 200
            this.httpRequest.process = false
            if (this.$auth.isAuth()) {
              var debounceLoginRedirection = _.debounce(() => {
                this.$store.dispatch('setUserToken', this.$auth.getUserToken())
                this.$router.push({name: 'dashboard'})
              }, 1000)
              debounceLoginRedirection()
            }
          } else {
            this.httpRequest.httpCode = 0
            this.httpRequest.message = 'User is currently ' + this.$auth.userStatus(response.data.status_id).name_type + '.'
            this.setOnLogin()
          }
        }).catch(error => {
          console.log(error)
          if (error.response) {
            this.httpRequest.httpCode = error.response.status
            if (error.response.data.message) {
              this.httpRequest.message = error.response.data.message
            } else {
              this.httpRequest.message = error.response.statusText
            }
          } else {
            this.httpRequest.httpCode = 0
            this.httpRequest.message = error.message
          }
          this.setOnLogin()
        })
      }).catch(error => {
        console.log(error)
        if (error.response) {
          console.log(error.response)
          this.httpRequest.httpCode = error.response.status
          if (error.response.data.message) {
            this.httpRequest.message = error.response.data.message
          } else {
            this.httpRequest.message = error.response.statusText
          }
        } else {
          this.httpRequest.httpCode = 0
          this.httpRequest.message = error.message
        }
        this.setOnLogin()
      })
    },

    setOnLogin () {
      var debounceLoginRedirection = _.debounce(() => {
        this.httpRequest.process = false
      }, 1000)
      debounceLoginRedirection()
    }

  }
}
</script>



