<template>
    <div>
        <div class="col-md-3">
            <div class="list-group">
                <router-link v-if="authUserStore.permission.create_user" class="list-group-item" :class="{'active' : $route.name === 'manageuser-create'}" :to="{name: 'manageuser-create'}">Create User</router-link>
                <router-link v-if="authUserStore.usergroup < 4" :class="{'list-group-item': true, 'active': $route.name === 'manageuser-all'}" :to="{name: 'manageuser-all'}">All Users</router-link>
                <router-link v-if="authUserStore.usergroup < 2" :class="{'list-group-item': true, 'active': $route.name === 'manageuser-ultimate'}" :to="{name: 'manageuser-ultimate'}">Ultimate Users</router-link>
                <router-link v-if="authUserStore.usergroup < 3" :class="{'list-group-item': true, 'active': $route.name === 'manageuser-premium'}" :to="{name: 'manageuser-premium'}">Premium Users</router-link>
                <router-link v-if="authUserStore.usergroup < 4" :class="{'list-group-item': true, 'active': $route.name === 'manageuser-reseller'}" :to="{name: 'manageuser-reseller'}">Reseller Users</router-link>
                <router-link v-if="authUserStore.usergroup < 5" :class="{'list-group-item': true, 'active': $route.name === 'manageuser-client'}" :to="{name: 'manageuser-client'}">Client Users</router-link>
            </div>
        </div><!-- end .col-md-3 -->
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default{
  computed: {
    ...mapState({
      authUserStore: state => state.authUserStore
    })
  }
}
</script>



