<template>
    <div>
        <nav class="navbar navbar-default" v-if="authUserStore.userToken !== null && authUserStore.userToken.access_token">
            <!-- Brand and toggle get grouped for better mobile display -->
            <div class="navbar-header">
                <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
                    <span class="sr-only">Toggle navigation</span>
                    <span class="icon-bar"></span>
                    <span class="icon-bar"></span>
                    <span class="icon-bar"></span>
                </button>
                <router-link class="navbar-brand" :to="{name: 'home'}">VPN Panel</router-link>
            </div>

            <!-- Collect the nav links, forms, and other content for toggling -->
            <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
                <ul class="nav navbar-nav">
                    <li><a href="#">Updates</a></li>
                    <li :class="{'active' : $route.name === 'dashboard' || $route.name === 'account-edit' || $route.name === 'account-security'}"><router-link :to="{name: 'dashboard'}">My Account</router-link></li>
                    <li v-if="authUserStore.permission.manage_user" :class="{'active' : $route.name === 'manageuser-all' || $route.name === 'manageuser-ultimate' || $route.name === 'manageuser-premium' || $route.name === 'manageuser-reseller' || $route.name === 'manageuser-client' || $route.name === 'user-profile' || $route.name === 'user-security' || $route.name === 'manageuser-create'}"><router-link :to="{name: 'manageuser-client'}">Manage Users</router-link></li>
                    <li class="dropdown">
                        <a href="#" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">Dropdown <span class="caret"></span></a>
                        <ul class="dropdown-menu">
                            <li><a href="#">Action</a></li>
                            <li><a href="#">Another action</a></li>
                            <li><a href="#">Something else here</a></li>
                            <li role="separator" class="divider"></li>
                            <li><a href="#">Separated link</a></li>
                            <li role="separator" class="divider"></li>
                            <li><a href="#">One more separated link</a></li>
                        </ul>
                    </li>
                </ul>
                <form class="navbar-form navbar-left">
                    <div class="form-group">
                        <input type="text" class="form-control" placeholder="Search">
                    </div>
                    <button type="submit" class="btn btn-default">Submit</button>
                </form>
                <ul class="nav navbar-nav navbar-right">
                    <li class="dropdown">
                        <a href="#" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">Account <span class="caret"></span></a>
                        <ul class="dropdown-menu">
                            <li><router-link to="/dashboard">Signed is as {{ authUserStore.username }}</router-link></li>
                            <li role="separator" class="divider"></li>
                            <li><a @click="logout" class="logout-class">Logout</a></li>
                        </ul>
                    </li>
                </ul>
            </div><!-- /.navbar-collapse -->
        </nav>

        <div v-if="!httpRequestStore.setHttpRequestMainApp && $route.name !== 'login' && !authUserStore.permission.update_account" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                Account is Limited Access.
            </div>
        </div>

    </div>

</template>

<style>
    .logout-class {
        cursor: pointer;
    }
</style>

<script>
import {mapState} from 'vuex'
export default{
  computed: {
    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  methods: {
    logout () {
      this.$auth.destroyUser()
      this.$store.dispatch('clearAuthUser')
      this.$router.push({name: 'login'})
    }
  }
}
</script>



