<template>
    <div>

        <div v-if="page_loader" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpRequestStore.httpRequestMainAppMessage }}
            </div>
        </div>

        <div v-if="page_load_status.error" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpRequestStore.httpRequestMainAppMessage }}
            </div>
        </div>

        <div v-if="page_display">

            <div class="page-header">
                <h2>Manage User - {{ username_display }}</h2>
            </div>

            <div class="row">

                <side-menu></side-menu>

                <div class="col-md-9">

                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title"><router-link :to="{ name: 'user-profile', params: { id: $route.params.id }}"><i class="glyphicon glyphicon-arrow-left"></i></router-link> User Apply Voucher</h3>
                        </div>

                        <div class="panel-body">

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <form class="form-horizontal" v-on:submit.prevent="uservoucher_trigger">

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.voucher_code}">
                                    <label for="voucher_code" class="col-sm-3 control-label">Voucher Code</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="voucher_code" name="voucher_code" v-model.trim="user.voucher_code" placeholder="Voucher Code">
                                        <small v-if="!process_loader" v-for="error in inputErrors.voucher_code" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <div class="col-sm-offset-3 col-sm-9">
                                        <button type="submit" class="btn btn-primary" :disabled="process_loader">Save</button>
                                    </div>
                                </div>

                            </form>

                        </div><!-- end .panel-body -->

                    </div><!-- end .panel .panel-default -->


                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title">User Vouchers</h3>
                        </div>

                        <div class="panel-body">


                            <form class="form-inline" v-on:submit.prevent="">
                                <div class="form-group">
                                    <label>Search</label>
                                </div>

                                <div class="form-group">
                                    <input type="text" class="form-control"
                                           placeholder="Search"
                                           v-model="query.search_input"
                                           @keyup.enter="fetchInitSearch()" :disabled="uservoucher_loader">
                                </div>

                            </form>

                            <div>&nbsp;</div>

                            <div v-if="uservoucher_loader" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    Loading user vouchers...
                                </div>
                            </div>

                            <div class="table-responsive" v-if="!uservoucher_loader">
                                <table class="table table-striped table-condensed table-hover">
                                    <thead>
                                    <tr>
                                        <th v-for="column in columns" @click="toggleOrder(column)">
                                            <span :class="{'label label-default' : column === query.column}">{{ column_transformer(column) }}</span>
                                            <span v-if="column === query.column">
                                                <i v-if="query.direction === 'desc'" class="fa fa-sort-amount-desc"></i>
                                                <i v-else class="fa fa-sort-amount-asc"></i>
                                            </span>
                                        </th>
                                        <th v-if="permission.is_admin">User Created</th>
                                        <th v-if="permission.is_admin">Action</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <tr v-for="row in model.data">
                                        <td>{{ row.code }}</td>
                                        <td>{{ row.duration }}</td>
                                        <td>{{ row.updated_at }}</td>
                                        <td>{{ row.created_at }}</td>
                                        <td v-if="permission.is_admin">{{ row.created_by }}</td>
                                        <td v-if="permission.is_admin"><i class="fa fa-remove"></i></td>
                                    </tr>
                                    <tr v-if="model.data && model.data.length === 0">
                                        <td colspan="6">No results</td>
                                    </tr>
                                    </tbody>
                                    <tfoot>
                                    <tr>
                                        <td colspan="6">

                                            <form class="form-inline" v-on:submit.prevent="">
                                                <div class="form-group">
                                                    <span>Displaying {{model.from}} - {{model.to}} of {{model.total}} rows Current Page: {{ model.current_page }} | </span>
                                                </div>

                                                <div class="form-group">
                                                    <button class="btn btn-default" @click="prev()">&laquo;</button>
                                                </div>
                                                <div class="form-group">
                                                    <button class="btn btn-default" @click="next()">&raquo;</button>
                                                </div>
                                            </form>
                                        </td>
                                    </tr>
                                    </tfoot>
                                </table>

                            </div>


                        </div><!-- end .panel-body -->

                    </div><!-- end .panel .panel-default -->

                </div><!-- end .col-md-9 -->

            </div><!-- end .row -->

        </div><!-- end v-if="!this.fetchUserDataStatus.loading && !this.fetchUserDataStatus.error && permission.update_account" -->
        <top-progress ref="topProgress"></top-progress>
    </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {accountUrl, userUrlVoucher, userUrlVoucherList, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default {
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    console.log('User Voucher created.')
    if (!this.$store.state.httpRequestStore.httpRequestMainApp) {
      this.$store.dispatch('setHttpRequestMainApp', true)
      this.$store.dispatch('setHttpRequestMainAppHttpCode', 0)
      this.$store.dispatch('setHttpRequestMainAppMessage', 'Accessing Page...')
      this.$store.dispatch('setHttpRequestMainAppAccess', false)
      this.fetchAccountData()
    }
    this.fetchUserData()
    this.fetchIndexVoucherData()
  },

  components: {
    SideMenu,
    topProgress
  },

  computed: {
    page_loader () {
      return this.$store.state.httpRequestStore.httpRequestMainApp || this.httpLoad.process || this.httpLoadVoucher.process
    },
    page_load_status () {
      if (!this.page_loader) {
        if (this.$store.state.httpRequestStore.httpRequestMainAppHttpCode === 200) {
          if (this.httpLoad.httpCode !== 200) {
            this.$refs.topProgress.fail()
            this.$store.dispatch('setHttpRequestMainAppMessage', this.httpLoad.message)
          } else if (this.httpLoadVoucher.httpCode !== 200) {
            this.$refs.topProgress.fail()
            this.$store.dispatch('setHttpRequestMainAppMessage', this.httpLoadVoucher.message)
          } else if (this.httpLoad.httpCode === 200 && this.httpLoadVoucher.httpCode === 200) {
            this.$refs.topProgress.done()
            this.$store.dispatch('setHttpRequestMainAppAccess', true)
          }
        } else {
          this.$refs.topProgress.fail()
        }
      }

      return {
        error: !this.page_loader && !this.$store.state.httpRequestStore.httpRequestMainAppAccess,
        message: this.$store.state.httpRequestStore.httpRequestMainAppMessage
      }
    },
    page_display () {
      return !this.page_loader && this.$store.state.httpRequestStore.httpRequestMainAppAccess
    },

    process_loader () {
      return this.httpRequest.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    uservoucher_loader () {
      return this.httpUserVoucher.process
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  data () {
    return {
      username_display: '',
      httpLoad: {
        process: true,
        httpCode: 0,
        message: null
      },
      httpLoadVoucher: {
        process: true,
        httpCode: 0,
        message: null
      },
      httpUserVoucher: {
        process: false,
        httpCode: 0,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      inputErrors: [],
      user: {
        voucher_code: null
      },
      model: {},
      columns: {},
      query: {
        page: 1,
        column: 'updated_at',
        direction: 'desc',
        per_page: 5,
        search_column: 'code',
        search_operator: 'equal',
        search_input: ''
      },
      operators: {
        equal: '=',
        like: 'LIKE',
        in: 'IN'
      },
      permission: {}
    }
  },

  methods: {
    hideMsg () {
      this.httpRequest.trigger = false
      this.inputErrors = []
    },

    uservoucher_trigger () {
      if (!this.process_loader) {
        this.httpRequest.message = 'Applying voucher code to user...'
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.edit_uservoucher(), 500)
        debounce()
      }
    },

    edit_uservoucher () {
      this.$http.post(userUrlVoucher(this.$route.params.id), this.user, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        this.httpRequest.httpCode = response.status

        var debounce = _.debounce(() => {
          this.user.voucher_code = ''
          this.query.search_input = ''
          this.query.column = 'updated_at'
          this.query.direction = 'desc'
          this.httpRequest.message = 'Voucher code applied to user.'
          this.fetchInitSearch()
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            errorMessage = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    fetchAccountData () {
      // this.fetchUserDataStatus.loading = true
      this.$http.get(accountUrl, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        this.$store.dispatch('setHttpRequestMainAppHttpCode', response.status)

        this.$store.dispatch('setUserName', response.data.username)
        this.$store.dispatch('setUserEmail', response.data.email)
        this.$store.dispatch('setUserFullname', response.data.fullname)
        this.$store.dispatch('setUserStatus', response.data.status_id)
        this.$store.dispatch('setUserGroup', response.data.user_group_id)
        this.$store.dispatch('setUserReg', response.data.created_at)
        this.$store.dispatch('setUserUpdate', response.data.updated_at)

        var permission = {}
        permission = response.data.permission
        this.$store.dispatch('setUserPermission', permission)

        this.$auth.setOneUserToken('username', response.data.username)

        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 500)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainAppHttpCode', errorHttpCode)
          this.$store.dispatch('setHttpRequestMainAppMessage', errorMessage)
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 500)
        debounce()
      })
    },

    fetchUserData () {
      this.$http.get(userUrlVoucher(this.$route.params.id), {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        this.httpLoad.httpCode = response.status

        this.username_display = response.data.user.username

        this.permission = response.data.permission

        var debounce = _.debounce(() => {
          this.httpLoad.process = false
        }, 2000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 2000)
        debounce()
      })
    },

    fetchIndexVoucherData () {
      this.$http.get(`${userUrlVoucherList(this.$route.params.id)}?column=${this.query.column}&direction=${this.query.direction}&page=${this.query.page}&per_page=${this.query.per_page}&search_column=${this.query.search_column}&search_operator=${this.query.search_operator}&search_input=${this.query.search_input}&status_id=${this.query.status_id}`, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        this.httpLoadVoucher.httpCode = response.status
        this.model = response.data.model
        this.columns = response.data.columns
        console.log(response.data.model)
        var debounce = _.debounce(() => {
          this.httpLoadVoucher.process = false
        }, 500)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpLoadVoucher.httpCode = errorHttpCode
          this.httpLoadVoucher.message = errorMessage
          this.httpLoadVoucher.process = false
        }, 500)
        debounce()
      })
    },

    column_transformer (col) {
      if (col === 'code') {
        return 'Voucher Code'
      }
      if (col === 'duration') {
        return 'Duration'
      }
      if (col === 'updated_at') {
        return 'Date Applied'
      }
      if (col === 'created_at') {
        return 'Date Created'
      }
      if (col === 'user_created') {
        return 'User Created'
      }
    },

    next () {
      if (this.model.next_page_url) {
        this.query.page++
        this.httpUserVoucher.process = true
        this.fetchSearchData()
      }
    },

    prev () {
      if (this.model.prev_page_url) {
        this.query.page--
        this.httpUserVoucher.process = true
        this.fetchSearchData()
      }
    },

    toggleOrder (column) {
      if (column === this.query.column) {
        // only change direction
        if (this.query.direction === 'desc') {
          this.query.direction = 'asc'
        } else {
          this.query.direction = 'desc'
        }
      } else {
        this.query.column = column
        this.query.direction = 'asc'
      }
      this.httpUserVoucher.process = true
      this.fetchSearchData()
    },

    fetchInitSearch () {
      if (!this.uservoucher_loader) {
        this.httpUserVoucher.process = true
        this.query.page = 1
        this.fetchSearchData()
      }
    },

    fetchSearchData () {
      this.$http.get(`${userUrlVoucherList(this.$route.params.id)}?column=${this.query.column}&direction=${this.query.direction}&page=${this.query.page}&per_page=${this.query.per_page}&search_column=${this.query.search_column}&search_operator=${this.query.search_operator}&search_input=${this.query.search_input}&status_id=${this.query.status_id}`, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        this.httpUserVoucher.httpCode = response.status
        this.model = response.data.model
        this.columns = response.data.columns
        console.log(response.data.model)
        var debounce = _.debounce(() => {
          this.httpUserVoucher.process = false
        }, 500)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpRequest.httpCode = errorHttpCode
          this.httpRequest.message = errorMessage
          this.httpRequest.process = false
        }, 500)
        debounce()
      })
    }
  }
}
</script>



