export const apiDomain = 'https://api.smarty-api.top/'
// export const apiDomain = 'http://vpn.dev/'
export const loginUrl = apiDomain + 'oauth/token'
export const accountUrl = apiDomain + 'api/account'
export const accountUrlUpdate = apiDomain + 'api/account/update'
export const passwordUrlUpdate = apiDomain + 'api/account/password'

export const userCreateUrl = apiDomain + 'api/manage-user/create'
export const userListUrl = function (userVar) {
  return apiDomain + 'api/manage-user/' + userVar
}
export const userUrl = function (userVar) {
  return apiDomain + 'api/manage-user/profile/' + userVar
}
export const userUrlSecurity = function (userVar) {
  return apiDomain + 'api/manage-user/security/' + userVar
}
export const userUrlPermision = function (userVar) {
  return apiDomain + 'api/manage-user/permission/' + userVar
}
export const userUrlPermisionToggle = function (userVar, pCode) {
  return apiDomain + 'api/manage-user/permission/' + userVar + '/' + pCode
}
export const userUrlDuration = function (userVar) {
  return apiDomain + 'api/manage-user/duration/' + userVar
}
export const userUrlCredits = function (userVar) {
  return apiDomain + 'api/manage-user/credits/' + userVar
}
export const userUrlVoucher = function (userVar) {
  return apiDomain + 'api/manage-user/voucher/' + userVar
}
export const userUrlVoucherList = function (userVar) {
  return apiDomain + 'api/manage-user/user-voucher/' + userVar
}

export const getHeader = function (AuthUser) {
  return {
    'Accept': 'Application/json',
    'Authorization': 'Bearer ' + AuthUser.access_token
  }
}



