const state = {
  httpRequestMainApp: false,
  httpRequestMainAppHttpCode: 0,
  httpRequestMainAppMessage: null,
  httpRequestMainAppAccess: false
}

const mutations = {
  httpRequestMainApp (state, val) {
    state.httpRequestMainApp = val
  },
  httpRequestMainAppHttpCode (state, val) {
    state.httpRequestMainAppHttpCode = val
  },
  httpRequestMainAppMessage (state, val) {
    state.httpRequestMainAppMessage = val
  },
  httpRequestMainAppAccess (state, val) {
    state.httpRequestMainAppAccess = val
  }
}

const actions = {
  setHttpRequestMainApp ({commit}, val) {
    commit('httpRequestMainApp', val)
  },
  setHttpRequestMainAppHttpCode ({commit}, val) {
    commit('httpRequestMainAppHttpCode', val)
  },
  setHttpRequestMainAppMessage ({commit}, val) {
    commit('httpRequestMainAppMessage', val)
  },
  setHttpRequestMainAppAccess ({commit}, val) {
    commit('httpRequestMainAppAccess', val)
  }
}

export default {
  state, mutations, actions
}



