const state = {
  userToken: null,
  username: null,
  email: null,
  fullname: null,
  status: 0,
  usergroup: 0,
  credits: null,
  expiration: null,
  datereg: null,
  lastupdate: null,
  permission: {}
}

const mutations = {
  SET_USER_TOKEN (state, userToken) {
    state.userToken = userToken
  },
  SET_USER_NAME (state, username) {
    state.username = username
  },
  SET_USER_EMAIL (state, email) {
    state.email = email
  },
  SET_USER_FULLNAME (state, fullname) {
    state.fullname = fullname
  },
  SET_USER_STATUS (state, status) {
    state.status = status
  },
  SET_USER_GROUP (state, usergroup) {
    state.usergroup = usergroup
  },
  SET_USER_CREDITS (state, credits) {
    state.credits = credits
  },
  SET_USER_EXPIRATION (state, expiration) {
    state.expiration = expiration
  },
  SET_USER_REG (state, datereg) {
    state.datereg = datereg
  },
  SET_USER_UPDATE (state, update) {
    state.lastupdate = update
  },
  SET_USER_PERMISSION (state, permission) {
    state.permission = permission
  },
  CLEAR_AUTH_USER (state) {
    state.userToken = null
    state.username = null
    state.email = null
    state.fullname = null
    state.status = 0
    state.usergroup = 0
    state.credits = null
    state.datereg = ''
    state.lastupdate = ''
  }
}

const actions = {
  setUserToken: ({commit}, userToken) => {
    commit('SET_USER_TOKEN', userToken)
  },
  setUserName: ({commit}, username) => {
    commit('SET_USER_NAME', username)
  },
  setUserEmail: ({commit}, email) => {
    commit('SET_USER_EMAIL', email)
  },
  setUserFullname: ({commit}, fullname) => {
    commit('SET_USER_FULLNAME', fullname)
  },
  setUserStatus: ({commit}, status) => {
    commit('SET_USER_STATUS', status)
  },
  setUserGroup: ({commit}, usergroup) => {
    commit('SET_USER_GROUP', usergroup)
  },
  setUserCredits: ({commit}, credits) => {
    commit('SET_USER_CREDITS', credits)
  },
  setUserExpiration: ({commit}, expiration) => {
    commit('SET_USER_EXPIRATION', expiration)
  },
  setUserReg: ({commit}, datereg) => {
    commit('SET_USER_REG', datereg)
  },
  setUserUpdate: ({commit}, update) => {
    commit('SET_USER_UPDATE', update)
  },
  setUserPermission: ({commit}, permission) => {
    commit('SET_USER_PERMISSION', permission)
  },
  clearAuthUser: ({commit}) => {
    commit('CLEAR_AUTH_USER')
  }
}

export default {
  state, mutations, actions
}



