<template>
    <div>

        <div v-if="page_loader" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                Accessing Page...
            </div>
        </div>

        <div v-if="page_load_status.error" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ page_load_status.message }}
            </div>
        </div>

        <div v-if="page_display">

            <div class="page-header">
                <h2>Dashboard - Edit Account</h2>
            </div>

            <div class="row">
                <dashboard-side></dashboard-side>
                <div class="col-md-9">
                    <div class="panel panel-default">
                        <div class="panel-heading">
                            <h3 class="panel-title">Edit Account &nbsp;<router-link v-show="!process_loader" :to="{name: 'dashboard'}"><i class="fa fa-arrow-left"></i></router-link></h3>
                        </div>
                        <div class="panel-body">

                            <div v-if="process_loader" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    Updating your account...
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ process_status.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && !process_status.error" class="success">
                                <div class="alert alert-dismissible alert-success" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    Account Updated.
                                </div>
                            </div>

                            <form class="form-horizontal" v-on:submit.prevent="editaccount_trigger">

                                <div v-if="authUserStore.permission.is_admin" :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.username}">
                                    <label for="username" class="col-sm-3 control-label">Username</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="username" name="username" v-model.trim="account.username" placeholder="Username">
                                        <small v-show="!process_loader" v-for="error in inputErrors.username" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.email}">
                                    <label for="email" class="col-sm-3 control-label">Email</label>
                                    <div class="col-sm-9">
                                        <input type="email" class="form-control" :disabled="process_loader" id="email" name="email" v-model.trim="account.email" placeholder="Email">
                                        <small v-show="!process_loader" v-for="error in inputErrors.email" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div :class="{'form-group' : true, 'has-error' : !process_loader && inputErrors.fullname}">
                                    <label for="fullname" class="col-sm-3 control-label">Fullname</label>
                                    <div class="col-sm-9">
                                        <input type="text" class="form-control" :disabled="process_loader" id="fullname" name="fullname" v-model.trim="account.fullname" placeholder="Fullname">
                                        <small v-show="!process_loader" v-for="error in inputErrors.fullname" class="help-block">
                                            <strong>{{ error }}</strong>
                                        </small>
                                    </div>
                                </div>

                                <div class="form-group">
                                    <div class="col-sm-offset-3 col-sm-9">
                                        <button type="submit" class="btn btn-primary" :disabled="process_loader">Edit</button>
                                    </div>
                                </div>

                            </form>

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->
                </div><!-- end .col-md-9 -->
            </div><!-- end .row -->

        </div><!-- v-if="!fetchUserDataStatus.loading && !fetchUserDataStatus.error && permission.update_account" -->

    </div>
</template>
<style>

</style>
<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import {accountUrl, accountUrlUpdate, getHeader} from './../config.js'
import DashboardSide from './DashboardSideBar.vue'

export default {
  created () {
    console.log('Edit Account created.')
    if (!this.$store.state.httpRequestStore.httpRequestMainApp) {
      this.$store.dispatch('setHttpRequestMainApp', true)
      this.$store.dispatch('setHttpRequestMainAppHttpCode', 0)
      this.$store.dispatch('setHttpRequestMainAppMessage', null)
      this.$store.dispatch('setHttpRequestMainAppAccess', false)
      this.fetchUserData()
    }
  },

  components: {
    DashboardSide
  },

  data () {
    return {
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      inputErrors: []
    }
  },

  computed: {
    page_loader () {
      return this.$store.state.httpRequestStore.httpRequestMainApp
    },
    page_load_status () {
      if (this.$store.state.httpRequestStore.httpRequestMainAppHttpCode === 200 && this.$store.state.authUserStore.permission.update_account) {
        this.$store.dispatch('setHttpRequestMainAppAccess', true)
      }
      if (this.$store.state.httpRequestStore.httpRequestMainAppHttpCode === 200 && !this.$store.state.authUserStore.permission.update_account) {
        this.$store.dispatch('setHttpRequestMainAppMessage', 'No permission to update your account.')
        this.$store.dispatch('setHttpRequestMainAppAccess', false)
      }
      return {
        error: !this.page_loader && !this.$store.state.httpRequestStore.httpRequestMainAppAccess,
        message: this.$store.state.httpRequestStore.httpRequestMainAppMessage
      }
    },
    page_display () {
      return !this.page_loader && this.$store.state.httpRequestStore.httpRequestMainAppAccess
    },

    process_loader () {
      return this.httpRequest.process
    },

    process_trigger () {
      return !this.process_loader && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.process_loader && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    account () {
      const account = {}
      if (this.$store.state.authUserStore.permission.is_admin) {
        account.username = this.$store.state.authUserStore.username
      }
      account.email = this.$store.state.authUserStore.email
      account.fullname = this.$store.state.authUserStore.fullname
      return account
    },
    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  methods: {
    hideMsg () {
      this.httpRequest.trigger = false
      this.inputErrors = []
    },

    editaccount_trigger () {
      if (!this.process_loader) {
        this.httpRequest.trigger = true
        this.httpRequest.process = true
        this.inputErrors = []
        var debounce = _.debounce(() => this.edit_account(), 500)
        debounce()
      }
    },

    edit_account () {
      this.$http.post(accountUrlUpdate, this.account, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)
        this.httpRequest.httpCode = response.status
        if (this.account.username) {
          this.$auth.setOneUserToken('username', this.account.username)
          this.$store.dispatch('setUserName', this.$auth.getUserToken().username)
        }

        var debounce = _.debounce(() => {
          this.httpRequest.process = false
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error)
        if (error.response) {
          this.httpRequest.httpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 422) {
            this.httpRequest.message = 'Input Error.'
            this.inputErrors = error.response.data
          } else if (error.response.status === 403) {
            this.httpRequest.message = error.response.data.message
          } else {
            this.httpRequest.message = 'Server Error.'
          }
        } else {
          this.httpRequest.httpCode = 0
          this.httpRequest.message = error.message
        }
        var debounce = _.debounce(() => {
          this.httpRequest.process = false
        }, 1000)
        debounce()
      })
    },

    fetchUserData () {
      console.log('Local fetchUserData')
      this.$http.get(accountUrl, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        this.$store.dispatch('setHttpRequestMainAppHttpCode', response.status)

        this.$store.dispatch('setUserName', response.data.username)
        this.$store.dispatch('setUserEmail', response.data.email)
        this.$store.dispatch('setUserFullname', response.data.fullname)
        this.$store.dispatch('setUserStatus', response.data.status_id)
        this.$store.dispatch('setUserGroup', response.data.user_group_id)
        this.$store.dispatch('setUserReg', response.data.created_at)
        this.$store.dispatch('setUserUpdate', response.data.updated_at)

        var permission = {}
        permission = response.data.permission
        this.$store.dispatch('setUserPermission', permission)

        this.$auth.setOneUserToken('username', response.data.username)

        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 500)
        debounce()
      }).catch(error => {
        console.log(error)
        if (error.response) {
          this.$store.dispatch('setHttpRequestMainAppHttpCode', error.response.status)
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            this.$store.dispatch('setHttpRequestMainAppMessage', error.response.data.message)
          } else {
            this.$store.dispatch('setHttpRequestMainAppMessage', error.response.statusText)
          }
        } else {
          this.$store.dispatch('setHttpRequestMainAppHttpCode', 0)
          this.$store.dispatch('setHttpRequestMainAppMessage', error.message)
        }
        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 500)
        debounce()
      })
    }
  }
}
</script>



