<template>
    <div>

        <div v-if="page_loader" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                {{ httpRequestStore.httpRequestMainAppMessage }}
            </div>
        </div>

        <div v-if="page_load_status.error" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ httpRequestStore.httpRequestMainAppMessage }}
            </div>
        </div>

        <div v-if="page_display">

            <div class="page-header">
                <h2>Manage User - Client Users</h2>
            </div>

            <div class="row">
                <side-menu></side-menu>
                <div class="col-md-9">

                    <div class="row">
                        <div class="col-md-6 col-sm-6 col-xs-12">
                            <div class="info-box">
                                <span class="info-box-icon bg-green"><i class="glyphicon glyphicon-user"></i></span>

                                <div class="info-box-content">
                                    <span class="info-box-text">Total Client Users</span>
                                    <span class="info-box-number">
                                        {{ total }}
                                    </span>
                                </div>
                                <!-- /.info-box-content -->
                            </div>
                            <!-- /.info-box -->
                        </div>
                        <div class="col-md-6 col-sm-6 col-xs-12">
                            <div class="info-box">
                                <span class="info-box-icon bg-aqua"><i class="glyphicon glyphicon-hourglass"></i></span>

                                <div class="info-box-content">
                                    <span class="info-box-text">New Client</span>
                                    <span class="info-box-number">
                                        {{ new_users }}
                                    </span>
                                </div>
                                <!-- /.info-box-content -->
                            </div>
                            <!-- /.info-box -->
                        </div>
                    </div>

                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title">View Client Users</h3>
                        </div><!-- end .panel-heading -->

                        <div class="panel-body">

                            <form class="form-inline" v-on:submit.prevent="">
                                <div class="form-group">
                                    <label>Search By</label>
                                    <select class="form-control" v-model="query.search_column" :disabled="httpRequest.process" @change="trans_op(query.search_column)">
                                        <option value="id" :selected="query.search_column === 'id'">ID</option>
                                        <option value="username">Username</option>
                                        <option value="email">Email</option>
                                    </select>
                                </div>
                                <div class="form-group">
                                    <select class="form-control" v-model="query.search_operator" :disabled="httpRequest.process">
                                        <option v-for="(value, key) in operators" :value="key">{{value}}</option>
                                    </select>
                                </div>

                                <div class="form-group">
                                    <input type="text" class="form-control"
                                           placeholder="Search"
                                           v-model="query.search_input"
                                           @keyup.enter="fetchInitSearch()" :disabled="httpRequest.process">
                                </div>

                                <div class="form-group">
                                    <div class="btn-group" role="group" aria-label="...">
                                        <button type="button" :disabled="httpRequest.process" @click="changeStatusDisplay(-1)" :class="{'btn' : true, 'btn-primary' : query.status_id === -1, 'btn-default' : query.status_id !== -1}">All</button>
                                        <button type="button" :disabled="httpRequest.process" @click="changeStatusDisplay(0)" :class="{'btn' : true, 'btn-primary' : query.status_id === 0, 'btn-default' : query.status_id !== 0}">Deactivated</button>
                                        <button type="button" :disabled="httpRequest.process" @click="changeStatusDisplay(1)" :class="{'btn' : true, 'btn-primary' : query.status_id === 1, 'btn-default' : query.status_id !== 1}">Active</button>
                                        <button type="button" :disabled="httpRequest.process" @click="changeStatusDisplay(2)" :class="{'btn' : true, 'btn-primary' : query.status_id === 2, 'btn-default' : query.status_id !== 2}">Suspended</button>
                                    </div>
                                </div>

                            </form>

                            <div>&nbsp;</div>

                            <div v-if="httpRequest.process" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div v-if="process_trigger && process_status.error" class="error">
                                <div class="alert alert-dismissible alert-danger" role="alert">
                                    <button type="button" class="close" @click="hideMsg"><span>&times;</span></button>
                                    {{ httpRequest.message }}
                                </div>
                            </div>

                            <div class="table-responsive" v-if="!httpRequest.process && httpRequest.httpCode === 200">
                                <table class="table table-striped table-condensed table-hover">
                                    <thead>
                                    <tr>
                                        <th v-for="column in columns" @click="toggleOrder(column)">
                                            <span :class="{'label label-default' : column === query.column}">{{ column_transformer(column) }}</span>
                                            <span v-if="column === query.column">
                                                <i v-if="query.direction === 'desc'" class="fa fa-sort-amount-desc"></i>
                                                <i v-else class="fa fa-sort-amount-asc"></i>
                                            </span>
                                        </th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <tr v-for="row in model.data">
                                        <td>{{ row.id }}</td>
                                        <td><router-link :to="{ name: 'user-profile', params: { id: row.id }}">{{ row.username }}</router-link></td>
                                        <td>{{ row.email }}</td>
                                        <td><span :class="'label label-' + $auth.userGroup(row.user_group_id).class_type">{{ $auth.userGroup(row.user_group_id).name_type }}</span></td>
                                        <td><span :class="'label label-' + $auth.userStatus(row.status_id).class_type">{{ $auth.userStatus(row.status_id).name_type }}</span></td>
                                        <td><span :class="{'label ': true, 'label-default': row.expired_at === 'Expired', 'label-success': row.expired_at !== 'Expired'}">{{ row.expired_at }}</span></td>
                                        <td><i class="fa fa-remove"></i></td>
                                    </tr>
                                    <tr v-if="model.data && model.data.length === 0">
                                        <td colspan="6">No results</td>
                                    </tr>
                                    </tbody>
                                    <tfoot>
                                    <tr>
                                        <td colspan="6">

                                            <form class="form-inline" v-on:submit.prevent="">
                                                <div class="form-group">
                                                    <span>Displaying {{model.from}} - {{model.to}} of {{model.total}} rows Current Page: {{ model.current_page }} | </span>
                                                </div>

                                                <div class="form-group">
                                                    <label>Go to page</label>
                                                    <input type="text" v-model="query.page"
                                                           class="form-control"
                                                           @keyup.enter="goToPage()">
                                                </div>
                                                <div class="form-group">
                                                    <button class="btn btn-default" @click="prev()">&laquo;</button>
                                                </div>
                                                <div class="form-group">
                                                    <button class="btn btn-default" @click="next()">&raquo;</button>
                                                </div>
                                            </form>
                                        </td>
                                    </tr>
                                    </tfoot>
                                </table>

                            </div>

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->
                </div><!-- end .col-md-6 -->
            </div><!-- end .row -->

        </div><!-- end v-if="page_display" -->
        <top-progress ref="topProgress"></top-progress>
    </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import topProgress from 'vue-top-progress'
import {accountUrl, userListUrl, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default{
  mounted () {
    this.$refs.topProgress.start()
  },
  created () {
    console.log('Client User created.')
    if (!this.$store.state.httpRequestStore.httpRequestMainApp) {
      this.$store.dispatch('setHttpRequestMainApp', true)
      this.$store.dispatch('setHttpRequestMainAppHttpCode', 0)
      this.$store.dispatch('setHttpRequestMainAppMessage', 'Accessing Page...')
      this.$store.dispatch('setHttpRequestMainAppAccess', false)
      this.fetchAccountData()
    }
    this.fetchIndexData()
  },

  components: {
    SideMenu,
    topProgress
  },

  data () {
    return {
      httpLoad: {
        process: true,
        httpCode: 0,
        message: null
      },
      httpRequest: {
        process: false,
        httpCode: 0,
        trigger: null,
        message: null
      },
      model: {},
      columns: {},
      total: 0,
      new_users: 0,
      query: {
        page: 1,
        column: 'id',
        direction: 'desc',
        per_page: 25,
        search_column: 'id',
        search_operator: 'equal',
        search_input: '',
        status_id: -1
      },
      operators: {
        equal: '=',
        not_equal: '<>',
        less_than: '<',
        greater_than: '>',
        less_than_or_equal_to: '<=',
        greater_than_or_equal_to: '>=',
        in: 'IN'
      }
    }
  },

  computed: {
    page_loader () {
      return this.$store.state.httpRequestStore.httpRequestMainApp || this.httpLoad.process
    },
    page_load_status () {
      if (!this.page_loader) {
        if (this.$store.state.httpRequestStore.httpRequestMainAppHttpCode === 200) {
          if (this.httpLoad.httpCode !== 200) {
            this.$refs.topProgress.fail()
            this.$store.dispatch('setHttpRequestMainAppMessage', this.httpLoad.message)
          } else if (this.httpLoad.httpCode === 200) {
            this.$refs.topProgress.done()
            this.$store.dispatch('setHttpRequestMainAppAccess', true)
          }
        } else {
          this.$refs.topProgress.fail()
        }
      }

      return {
        error: !this.page_loader && !this.$store.state.httpRequestStore.httpRequestMainAppAccess,
        message: this.$store.state.httpRequestStore.httpRequestMainAppMessage
      }
    },

    page_display () {
      return !this.page_loader && this.$store.state.httpRequestStore.httpRequestMainAppAccess
    },

    process_trigger () {
      return !this.httpRequest.process && this.httpRequest.trigger
    },

    process_status () {
      return {
        error: !this.httpRequest.process && this.httpRequest.httpCode !== 200,
        message: this.httpRequest.message
      }
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  methods: {
    fetchAccountData () {
      this.$http.get(accountUrl, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        this.$store.dispatch('setHttpRequestMainAppHttpCode', response.status)

        this.$store.dispatch('setUserName', response.data.username)
        this.$store.dispatch('setUserEmail', response.data.email)
        this.$store.dispatch('setUserFullname', response.data.fullname)
        this.$store.dispatch('setUserStatus', response.data.status_id)
        this.$store.dispatch('setUserGroup', response.data.user_group_id)
        this.$store.dispatch('setUserReg', response.data.created_at)
        this.$store.dispatch('setUserUpdate', response.data.updated_at)

        var permission = {}
        permission = response.data.permission
        this.$store.dispatch('setUserPermission', permission)

        this.$auth.setOneUserToken('username', response.data.username)

        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainAppHttpCode', errorHttpCode)
          this.$store.dispatch('setHttpRequestMainAppMessage', errorMessage)
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 500)
        debounce()
      })
    },

    changeStatusDisplay (statusId) {
      this.query.status_id = statusId
      this.fetchInitSearch()
    },

    trans_op (col) {
      if (col === 'id') {
        this.operators = {}
        this.operators.equal = '='
        this.operators.not_equal = '<>'
        this.operators.less_than = '<'
        this.operators.greater_than = '>'
        this.operators.less_than_or_equal_to = '<='
        this.operators.greater_than_or_equal_to = '>='
        this.operators.in = 'IN'
      } else if (col === 'username' || col === 'email') {
        this.operators = {}
        this.operators.equal = '='
        this.operators.not_equal = '<>'
        this.operators.in = 'IN'
        this.operators.like = 'LIKE'
      }
    },

    column_transformer (col) {
      if (col === 'id') {
        return 'ID'
      }
      if (col === 'username') {
        return 'Username'
      }
      if (col === 'email') {
        return 'Email'
      }
      if (col === 'user_group_id') {
        return 'User Group'
      }
      if (col === 'status_id') {
        return 'Status'
      }
      if (col === 'expired_at') {
        return 'Duration'
      }
    },

    next () {
      if (this.model.next_page_url) {
        this.query.page++
        this.fetchSearchData()
      }
    },
    prev () {
      if (this.model.prev_page_url) {
        this.query.page--
        this.fetchSearchData()
      }
    },
    toggleOrder (column) {
      if (!this.httpRequest.process) {
        this.httpRequest.message = 'Reordering...'
        if (column === this.query.column) {
          // only change direction
          if (this.query.direction === 'desc') {
            this.query.direction = 'asc'
          } else {
            this.query.direction = 'desc'
          }
        } else {
          this.query.column = column
          this.query.direction = 'asc'
        }
        this.fetchSearchData()
      }
    },
    fetchInitSearch () {
      this.query.page = 1
      this.fetchSearchData()
    },
    goToPage () {
      this.fetchSearchData()
    },
    fetchIndexData () {
      this.$http.get(`${userListUrl('client')}?column=${this.query.column}&direction=${this.query.direction}&page=${this.query.page}&per_page=${this.query.per_page}&search_column=${this.query.search_column}&search_operator=${this.query.search_operator}&search_input=${this.query.search_input}&status_id=${this.query.status_id}`, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        this.httpLoad.httpCode = response.status
        this.httpRequest.httpCode = response.status
        this.model = response.data.model
        this.columns = response.data.columns
        this.total = response.data.total
        this.new_users = response.data.new_users
        console.log(response.data.model)
        var debounce = _.debounce(() => {
          this.httpLoad.process = false
        }, 500)
        debounce()
      }).catch(error => {
        console.log(error.response)
        var errorMessage = 'Server Error.'
        var errorHttpCode = 0
        if (error.response) {
          errorHttpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.data.message) {
            errorMessage = error.response.data.message
          }
        }
        var debounce = _.debounce(() => {
          this.httpLoad.httpCode = errorHttpCode
          this.httpLoad.message = errorMessage
          this.httpLoad.process = false
        }, 500)
        debounce()
      })
    },

    fetchSearchData () {
      if (!this.httpRequest.process) {
        this.httpRequest.message = 'Fetching data...'
        this.httpRequest.process = true
        this.httpRequest.trigger = true
        this.$http.get(`${userListUrl('client')}?column=${this.query.column}&direction=${this.query.direction}&page=${this.query.page}&per_page=${this.query.per_page}&search_column=${this.query.search_column}&search_operator=${this.query.search_operator}&search_input=${this.query.search_input}&status_id=${this.query.status_id}`, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
          this.httpRequest.httpCode = response.status
          this.model = response.data.model
          this.columns = response.data.columns
          console.log(response.data.model)
          var debounce = _.debounce(() => {
            this.httpRequest.process = false
            this.httpRequest.trigger = false
          }, 500)
          debounce()
        }).catch(error => {
          console.log(error.response)
          var errorMessage = 'Server Error.'
          var errorHttpCode = 0
          if (error.response) {
            errorHttpCode = error.response.status
            if (error.response.status === 401) {
              this.$auth.destroyUser()
              this.$store.dispatch('clearAuthUser')
              this.$router.push({name: 'login'})
            }
            if (error.response.data.message) {
              errorMessage = error.response.data.message
            }
          }
          var debounce = _.debounce(() => {
            this.httpRequest.httpCode = errorHttpCode
            this.httpRequest.message = errorMessage
            this.httpRequest.process = false
          }, 500)
          debounce()
        })
      }
    },
    hideMsg () {
      this.httpRequest.trigger = false
    }
  }
}
</script>



